import { Component, OnInit } from '@angular/core';
import { LoginService } from '../services/login/login';
import { AuthenticateModel, IAuthenticateModel, AuthenticateResultModel } from '../models/AuthenticateModel';
import { ActivatedRoute, Router } from '@angular/router';
import { Storage          } from '@ionic/storage';
import { LoadingService } from '../services/loading/loading.service';
import { TranslateService } from '@ngx-translate/core';
import { SettingsService } from '../services/Settings/Settings';
import { TempDataDistributorService } from '../services/TempDataDistributor/TempDataDistributor';



@Component({
  selector: 'app-login-verification-code',
  templateUrl: './login-verification-code.page.html',
  styleUrls: ['./login-verification-code.page.scss'],
})
export class LoginVerificationCodePage implements OnInit {

  vCode;

  UserName:string ;
  authenticateResult;

  C1;C2;C3;C4;C5;C6;

  currentLang : string = 'ar';

  constructor(
    private _login       : LoginService,
    private activeRouter : ActivatedRoute,
    private router       : Router ,
    private storage      : Storage ,
    private _loading     : LoadingService,
    private _translate   : TranslateService,
    private _setting     : SettingsService,
    private _tempData    : TempDataDistributorService
    // private _tokenService: TokenService
  ) { }

  ngOnInit() {
    if(this.activeRouter.snapshot.data['dataSharing']){
      this.UserName = this.activeRouter.snapshot.data['dataSharing'];
     // this.vCode = this.activeRouter.snapshot.data['dataSharing'].verificationCode;
      console.log(this.UserName)
    }
  }

  LoginOk() {

    this._loading.present();

    this.vCode = this.C1+this.C2+this.C3+this.C4+this.C5+this.C6;//mobile Code .
    console.log(this.vCode)

    let modelResult: IAuthenticateModel = {//Info for Login...
      password: this.vCode,
      rememberClient: true,
      userNameOrEmailAddress:this.UserName,
      returnUrl: "tabs/tab3",
      twoFactorRememberClientToken: "",
      twoFactorVerificationCode: "",
      singleSignIn: false
    }

    let userInfoForLogin = new AuthenticateModel(modelResult);//send Info for Login...

    this._login.authenticate(userInfoForLogin).subscribe((result) => {

      this.getCurrentUserLanguage();
       setTimeout(() => {
        this._loading.dismiss();
        this.processAuthenticateResult(result.result, result.result.returnUrl); // process Authenticate Result
       }, 1500);
      
      console.log(result);
      window.localStorage.setItem('userId' ,result.result.userId.toString());
      

    }, error => {
      this._loading.dismiss();
      console.log(error)
    })
  }


  private processAuthenticateResult(authenticateResult: AuthenticateResultModel, redirectUrl?: string) {// process Authenticate Result fun
    this.authenticateResult = authenticateResult;
    if (authenticateResult.accessToken) {
      // Successfully logged in

      this._tempData.authState.next(true);

      if (authenticateResult.returnUrl && !redirectUrl) {
        redirectUrl = authenticateResult.returnUrl;
        console.log(redirectUrl)
        console.log(" Successfully logged in")
      }

      this.login(
        authenticateResult.accessToken,
        authenticateResult.encryptedAccessToken,
        authenticateResult.expireInSeconds,
        true,
        authenticateResult.twoFactorRememberClientToken,
        redirectUrl
      );

    } else {
      // Unexpected result!

      this._tempData.authState.next(false);
      console.log('Unexpected authenticateResult!');
      this.router.navigate(['/login']);

    }
  }


  private login(accessToken: string, encryptedAccessToken: string, expireInSeconds: number, rememberMe?: boolean, twoFactorRememberClientToken?: string, redirectUrl?: string): void {

    let tokenExpireDate = rememberMe ? (new Date(new Date().getTime() + 1000 * expireInSeconds)) : undefined;

    this.storage.set('accessToken' , accessToken);

    console.log(tokenExpireDate);

    window.localStorage.setItem('accessToken' , accessToken);
    window.localStorage.setItem('tokenExpireDate' , tokenExpireDate.toString());
  
  
 
    if (redirectUrl) {
      this._tempData.authState.next(true);
      this._translate.use(this.currentLang);
      window.localStorage.setItem('lang',this.currentLang)
      //location.href = redirectUrl;
      this.router.navigateByUrl(redirectUrl);
    } else {
     // location.href = '/login';
      this.router.navigateByUrl('/login');
    }
  }

  getCurrentUserLanguage(){
    this._setting.getCurrentUserLanguage().subscribe(lang =>{
      this.currentLang = lang.result;
      console.log(this.currentLang);
    })
  }


  changeC1(e){
    console.log(e.data)
    //this.C1 = e.data
    if(parseInt(e.data) >= 0 && parseInt(e.data) <= 9)
    document.getElementById("VC2").focus();
  }
  changeC2(e){
    console.log(e.data)
  //  this.C2 = e.data
  if(parseInt(e.data) >= 0 && parseInt(e.data) <= 9)
    document.getElementById("VC3").focus();
  }
  changeC3(e){
    console.log(e.data)
   // this.C3 = e.data
   if(parseInt(e.data) >= 0 && parseInt(e.data) <= 9)
    document.getElementById("VC4").focus();
  }
  changeC4(e){
    console.log(e.data)
  //  this.C4 = e.data
  if(parseInt(e.data) >= 0 && parseInt(e.data) <= 9)
    document.getElementById("VC5").focus();
  }
  changeC5(e){
    console.log(e.data)
   // this.C5 = e.data
   if(parseInt(e.data) >= 0 && parseInt(e.data) <= 9)
    document.getElementById("VC6").focus();
  }
  changeC6(e){
    console.log(e.data)
  //  this.C6 = e.data
   // document.getElementById("VC6").focus();
  }

}
